@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,500;1,100;1,200;1,400;1,500&display=swap);
/*----------menu mobile -------*/

.z-index-2 {
    z-index: 2;
}

.overlay {
    z-index: 2;
    position: fixed;
    bottom: 0;
    top: 0;
    width: 100%;
    color: #000;
    background-color: #fff;
}

.close-menu {
    position: absolute;
    top: 2%;
    right: 5%;
}
body {
    margin: 0;
    font-family: "Raleway";
    text-align: center;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.link-menu {
    display: inline-block;
    font-family: "Raleway";
}

.part-1 {
    position: relative;
    top: 25%;
    width: 35%;
}

a {
    text-decoration: none;
    color: #000;
}

body,
html {
    height: 100%;
}

.photo-boutique {
    width: 550px;
    height: auto;
}

.photo-produit {
    width: 100%;
    height: auto;
}

.cursor-pointer {
    cursor: pointer;
}

.opacity-0 {
    opacity: 0;
}

.opacity-1 {
    opacity: 1;
}

.photo-panier {
    width: 100px;
    height: auto;
}

.photo-accueil {
    width: 450px;
    height: auto;
}

.line-heigt-small {
    line-height: 5px;
}

.w-31 {
    width: 31%;
}

.letter-spacing-3 {
    letter-spacing: 3px;
}

.font-weight-400 {
    font-weight: 400;
}

.w-70 {
    width: 70%;
}

.btn-acheter:hover {
    background-color: #373737;
}

.font-size-14 {
    font-size: 14px;
}

.w-18 {
    width: 18%;
}

.center-div {
    margin: 0 auto;
    float: none;
}
